"use client"

import { useState, useEffect } from "react"
import StatusBar from "./status-bar"
import AppIcon from "./app-icon"
import {
  Camera,
  ImageIcon,
  Phone,
  MessageCircle,
  Chrome,
  ShoppingBag,
  FileText,
  Music,
  Settings,
  Mail,
  Calendar,
  Map,
} from "lucide-react"

interface HomeScreenProps {
  isDarkMode: boolean
  onAppClick?: (appId: string) => void
}

export default function HomeScreen({ isDarkMode, onAppClick }: HomeScreenProps) {
  const [currentTime, setCurrentTime] = useState(new Date())

  useEffect(() => {
    const timer = setInterval(() => setCurrentTime(new Date()), 1000)
    return () => clearInterval(timer)
  }, [])

  const apps = [
    { 
      id: "photos",
      name: "Photos",
      icon: "/photos.png",
      iconClassName: "w-14 h-14",
      color: "bg-gradient-to-br from-red-400 to-yellow-400"
    },

    { 
      id: "camera",
      name: "Camera",
      icon: "/camera-icon.png",
      iconClassName: "w-14 h-14",
      color: "bg-gradient-to-br from-slate-600 to-slate-800" 
    },

    { 
      id: "phone",
      name: "Phone",
      icon: "/contacts-icon.svg",
      iconClassName: "w-14 h-14",
      color: "bg-gradient-to-br from-green-400 to-green-600"
     },

    {
      id: "messages",
      name: "Messages",
      icon: "/message-icon.png",
      iconClassName: "w-14 h-14",
      color: "bg-gradient-to-br from-green-400 to-green-600",
      badge: 3,
    },

    {
      id: "mail",
      name: "Mail",
      icon: "/mail-icon.png",
      iconClassName: "w-14 h-14",
      color: "bg-gradient-to-br from-blue-500 to-blue-600",
      badge: 12,
    },

    { 
      id: "safari",
      name: "Safari",
      icon: "/safari-icon.png",
      iconClassName: "w-14 h-14",
      color: "bg-gradient-to-br from-blue-400 to-cyan-500"
    },

    { 
      id: "maps",
      name: "Maps",
      icon: "/maps-icon.svg",
      iconClassName: "w-14 h-14",
      color: "bg-gradient-to-br from-green-400 to-green-600"
     },

    { 
      id: "calendar", 
      name: "Calendar", 
      icon: "/calendar-icon.png", 
      color: "bg-white dark:bg-slate-800",
      iconClassName: "w-14 h-14" },

    { id: "notes", 
      name: "Notes", 
      icon: "/notes-icon.png", 
      iconClassName: "w-14 h-14",
      color: "bg-gradient-to-br from-yellow-300 to-yellow-500" },

    { 
      id: "music", 
      name: "Music", 
      icon: "/apple-music.png",
      iconClassName: "w-14 h-14",
      color: "bg-gradient-to-br from-pink-500 to-red-500" },
      {
        id: "appstore",
        name: "App Store",
        icon: "appstore-icon.svg",
        iconClassName: "w-14 h-14",
      color: "bg-gradient-to-br from-blue-500 to-blue-600",
      badge: 1,
    },
    
    { 
      id: "settings", 
      name: "Settings", 
      icon: "/settings-icon.png",
      iconClassName: "w-14 h-14", 
      color: "bg-gradient-to-br from-slate-600 to-slate-700" 
    },
  ]

  const getGreeting = () => {
    const hour = currentTime.getHours()
    if (hour < 12) return "Good Morning"
    if (hour < 18) return "Good Afternoon"
    return "Good Evening"
  }

  return (
    <div className="relative w-full h-full bg-gradient-to-b from-blue-50 via-purple-50 to-pink-50 dark:from-slate-900 dark:via-purple-950 dark:to-slate-950 transition-colors duration-500 animate-fade-in">
      <StatusBar isDarkMode={isDarkMode} />

      <div className="px-6 pt-16 pb-2">
        <div className="glass-effect rounded-2xl p-4 mb-4 border border-white/20 dark:border-white/10 hover-lift">
          <h2 className="text-2xl font-bold mb-1">{getGreeting()}</h2>
          <p className="text-sm text-muted-foreground">Welcome to my interactive portfolio</p>
        </div>
      </div>

      {/* App Grid */}
      <div className="px-6 pb-24">
        <div className="grid grid-cols-4 gap-6">
          {apps.map((app, index) => (
            <div
              key={app.id}
              className="animate-fade-in"
              style={{
                animationDelay: `${index * 0.05}s`,
              }}
            >
              <AppIcon
                name={app.name}
                icon={app.icon}
                color={app.color}
                iconClassName={app.iconClassName}
                onClick={() => onAppClick?.(app.id)}
                badge={app.badge}
              />
            </div>
          ))}
        </div>
      </div>

      {/* Dock */}
      <div className="absolute bottom-6 left-6 right-6">
        <div className="glass-effect rounded-3xl p-4 shadow-lg border border-white/20 dark:border-white/10 animate-slide-up">
          <div className="grid grid-cols-4 gap-4">

            <AppIcon
              name="Phone"
              icon="/phone-icon.png"
              iconClassName="w-14 h-14"
              color="bg-gradient-to-br from-green-400 to-green-600"
              showLabel={false}
              onClick={() => onAppClick?.("phone")}
            />
            <AppIcon
              name="Safari"
              icon= "/message-icon.png"
              iconClassName="w-14 h-14"
              color="bg-gradient-to-br from-blue-400 to-cyan-500"
              showLabel={false}
              onClick={() => onAppClick?.("safari")}
            />
            <AppIcon
              name="Messages"
              icon="/message-icon.png"
              iconClassName="w-14 h-14"
              color="bg-gradient-to-br from-green-400 to-green-600"
              showLabel={false}
              onClick={() => onAppClick?.("messages")}
              badge={3}
            />
            <AppIcon
              name="Music"
              icon="/icon-dark-32x32.png"
              color="bg-gradient-to-br from-pink-500 to-red-500"
              showLabel={false}
              onClick={() => onAppClick?.("music")}
            />
          </div>
        </div>
      </div>

      {/* Page Indicator */}
      <div className="absolute bottom-24 left-1/2 -translate-x-1/2 flex gap-2">
        <div className="w-2 h-2 rounded-full bg-foreground" />
        <div className="w-2 h-2 rounded-full bg-foreground/30" />
      </div>
    </div>
  )
}
